body {
  background-color: #faf9f4;
  color: #222;
  font-family: "Arial", sans-serif;
}

a {
  text-decoration: none;
  color: #222;
}

header {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: justify;
  -moz-box-pack: justify;
  -ms-flex-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  width: 100%;
  padding: 0 30px;
  align-items: baseline;
  background-color: #FAF9F4;
  font-size: 14px;
}
header .top-logo {
  width: 200px;
  height: 30px;
  background-color: #CCC;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -moz-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -moz-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  text-decoration: none;
  color: #222;
  border-radius: 5px;
}
header ul {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  list-style-type: none;
}
header ul:first-child {
  margin-right: 20px;
}
header ul li {
  padding: 10px;
  margin: 8px 5px;
  cursor: pointer;
}
header ul li:active, header ul li:hover {
  transition: background-color 0.3s ease;
  background-color: #EBC201;
  color: #FFF;
  border-radius: 5px;
}

section.content {
  background-color: #FFF;
  box-shadow: 0 1px 5px #999;
  border-radius: 5px;
}
section.content .main-title {
  padding: 35px 30px;
  margin: 0;
}

article.featured-book {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: justify;
  -moz-box-pack: justify;
  -ms-flex-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  padding: 30px;
  background-color: #faf9f4;
  position: relative;
  overflow: hidden;
}
article.featured-book .recommended {
  position: absolute;
  top: -3px;
  right: -5px;
  background-color: #EBC201;
  color: #FFF;
  padding: 6px 12px 2px 9px;
  border-radius: 6px;
  font-size: 12px;
}
article.featured-book img {
  width: 100%;
  max-width: 265px;
  height: auto;
  border-radius: 10px;
}
article.featured-book .book-info {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-direction: normal;
  -webkit-box-orient: vertical;
  -moz-box-direction: normal;
  -moz-box-orient: vertical;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  width: 70%;
  padding: 0 20px;
}
article.featured-book .book-info h2 {
  font-size: 32px;
  font-weight: bold;
  margin: 0;
}
article.featured-book .book-info p {
  font-size: 14px;
  margin: 10px 0;
}

article.top-voted h3 {
  padding: 35px 30px;
  margin: 0;
  font-weight: bolder;
}
article.top-voted .book {
  margin: 25px;
  padding: 16px;
  position: relative;
  background-color: #faf9f4;
  min-height: 570px;
}
article.top-voted .book .position {
  position: absolute;
  top: 10px;
  right: 10px;
  background: white;
  color: #EBC201;
  border-radius: 100%;
  padding: 7px;
  font-weight: bold;
  font-size: 16px;
  width: 30px;
  height: 30px;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -moz-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -moz-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  border: 2px solid #EBC201;
}
article.top-voted .book figure {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -moz-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
}
article.top-voted .book figure img {
  width: 100%;
  max-width: 135px;
  height: auto;
  border-radius: 10px;
}
article.top-voted .book .book-info .title {
  font-size: 18px;
  font-weight: bolder;
  margin-bottom: 5px;
}
article.top-voted .book .author {
  font-size: 16px;
  margin-bottom: 25px;
}
article.top-voted .book .author,
article.top-voted .book .synopsis {
  font-size: 16px;
}
article.top-voted .book .synopsis {
  font-size: 14px;
}
article.top-voted .book .synopsis p {
  margin-bottom: 0;
}

footer {
  background-color: #292929;
  color: #FFF;
  padding: 15px;
  text-align: center;
  position: sticky;
  left: 0;
  bottom: 0;
  width: 100%;
}
footer ul {
  list-style-type: none;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -moz-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  padding: 0;
  margin-bottom: 0;
}
footer ul li {
  padding: 10px;
  margin: 0 30px;
  cursor: pointer;
}
footer ul li:active, footer ul li:hover {
  transition: color 0.3s ease;
  color: #EBC201;
  border-radius: 5px;
}